<template>
  <div>

    <div style="position:relative;">
      <img src="../assets/bgi.jpeg" alt="" style="height: 200px; width: 100%">
      <div style="position:absolute;line-height: 200px;font-size: 48px;top:0;left:42%;color: white;">{{title}}</div>
    </div>
    <div>
      <router-link :to="{path:'/info',query: {title: 'PIANO',id:'app'}}" v-if="title === 'PIANO'">
        <Card title="APP设计" :padding="0" shadow style="width: 50%; height: 350px; float: left;">
          <img src="../assets/pan/app/app.jpg" alt="" style="width: 80%;">
          <div style="padding: 15px">产品风格简单明了，围绕读书创作功能，使用合理布局排版，使用门槛低；为了提高用户浏览时的舒适度，对界面布局的间距进行统一规范；合理留白的意义在于能大幅度提高用户浏览信息时的舒适度和效率。</div>
        </Card>
      </router-link>
      <router-link :to="{path:'/info',query: {title: 'PIANO',id:'web'}}" v-if="title === 'PIANO'">
        <Card title="WEB网页设计" :padding="0" shadow style="width: 50%; height: 350px; float: left;">
          <img src="../assets/pan/web/web.jpg" alt="" style="width: 80%;">
          <div  style="padding: 15px">样式上运用简约版式，易于使用，内容则采用多数据结构分析，让用户轻松舒适的浏览。
          </div>
        </Card>
      </router-link>

      <router-link :to="{path:'/info',query: {title: '一兜糖',id:''}}" v-if="title === '一兜糖'">
        <Card title="一兜糖" :padding="0" shadow style="width: 50%; height: 350px; float: left;">
          <img src="../assets/ydt/yidoutang.jpg" alt="" style="width: 80%;">
          <div  style="padding: 15px"></div>
        </Card>
      </router-link>

    </div>
  </div>
</template>
<script>
    export default {
        data(){
            return {
                title: 'PIANO',
            }
        },
        mounted() {
        },
        watch: {
            $route( to , from ){
                console.log(to)
                const param = to.query.name;
                switch (param) {
                    case 'ydt':
                        this.title = '一兜糖';
                        break;
                    // case 'dwlx':
                    //     this.title = '蝶舞兰心';
                    //     break;
                    case 'pan':
                        this.title = 'PIANO';
                        break;
                    // default:
                    //     this.title = 'PIANO';
                }
            }
        }
    }
</script>

<style scoped>

</style>



