<template>
  <div id="app">
    <Menu mode="horizontal" :active-name=activeIndex class="menuContainer">
      <Row>
<!--        <Col :xs="2" :sm="4" :md="6" :lg="8">Col</Col>-->
<!--        <Col :xs="20" :sm="16" :md="12" :lg="8">Col</Col>-->

        <Col :xs="24" :sm="4" :md="6" :lg="8" >
          <img src="./assets/erweima.jpg" alt="" class="erweima">
        </Col>

        <Col :xs="24" :sm="20" :md="18" :lg="16">
          <div style="float: right;">
            <MenuItem name="1" to="/" >
              <Icon type="ios-paper"/>
              <Tooltip content="嘿！" placement="bottom">
                主页
              </Tooltip>
            </MenuItem>
            <MenuItem name="2" to="/about">
              <Icon type="ios-people"/>
              <Tooltip content="了解一下" placement="bottom">
                关于
              </Tooltip>
            </MenuItem>
            <Submenu name="3">
              <template slot="title">
                <Icon type="ios-stats"/>
                项目
              </template>
              <MenuItem name="3-1" :to="{path:'/project',query: {name: 'pan'}}">皮阿诺</MenuItem>
              <MenuItem name="3-2" :to="{path:'/project',query: {name: 'ydt'}}">一兜糖</MenuItem>
              <!--        <MenuItem name="3-3" :to="{path:'/project',query: {name: 'dwlx'}}">蝶舞兰心</MenuItem>-->
            </Submenu>
            <MenuItem name="4" to="/others">
              <Icon type="ios-calendar" />
              <Tooltip content="其他作品" placement="bottom">
                其他
              </Tooltip>
            </MenuItem>
            <MenuItem name="5" to="/contact">
              <Icon type="ios-construct"/>
              <Tooltip content="想找我？" placement="bottom">
                联系
              </Tooltip>
            </MenuItem>
          </div>
        </Col>
      </Row>
    </Menu>
    <router-view/>
  </div>
</template>

<script>
    export default {
        name: 'App',
        data() {
            return {
                activeIndex:'1'
            }
        },
        methods: {},
        watch:{
            $route(to,from){
                if(to.path === '/about'){
                    this.activeIndex = '2'
                }
            }
        },
    }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
  }

  .menuContainer {
    position: relative;
  }

  .erweima {
    /*position: absolute;*/
    /*left: 100px;*/
    float: left;
    margin-left: 30px;
    height: 60px;
  }

  .top,.bottom{
    text-align: center;
  }
  .center{
    width: 300px;
    margin: 10px auto;
    overflow: hidden;
  }
  .center-left{
    float: left;
  }
  .center-right{
    float: right;
  }

</style>



